<template>
  <div v-clock class="seePage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>公司三包</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">三包订单</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>三包订单详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form :inline="true" :model="params" class="demo-form-inline">
      <el-form-item label="三包订单单据号:" size="mini">
        {{params.order_sn}}
      </el-form-item>
      <el-form-item label="销售组织:" size="mini">
        {{params.company_name}}
      </el-form-item>
      <el-form-item label="客户名称:" size="mini">
        {{params.customer_name}}
      </el-form-item>
      <el-form-item label="业务日期:" size="mini">
        {{params.business_time}}
      </el-form-item>
      <el-form-item label="部门:" size="mini">
        {{params.department_name}}
      </el-form-item>
      <el-form-item label="业务员:" size="mini">
        {{params.business_name}}
      </el-form-item>
      <el-form-item label="审核状态:" size="mini">
        {{params.auditor_status_name}}
      </el-form-item>
      <el-form-item label="出库类型:" size="mini">
        {{params.outstorage_type_name}}
      </el-form-item>
      <el-form-item label="送货需求:" size="mini">
        {{params.delivery_demand_name}}
      </el-form-item>
      <el-form-item label="交货方式:" size="mini">
        {{params.hand_over_name}}
      </el-form-item>
      <el-form-item label="回款方式:" size="mini">
        <span v-if="params.payment_method==2">物流代收</span>
        <span v-else>客户自结</span>
      </el-form-item>
      <el-form-item label="物流公司:" size="mini" v-if="params.payment_method==2">
        {{params.settlement_customer_name}}
      </el-form-item>
      <el-form-item label="所属区域:" size="mini">
        {{params.province_name}}&nbsp;&nbsp;{{params.city_name}}&nbsp;&nbsp;{{params.area_name}}
      </el-form-item>
      <el-form-item label="收货人:" size="mini">
        {{params.consignee_name}}
      </el-form-item>
      <el-form-item label="手机号码:" size="mini">
        {{params.consignee_mobile}}
      </el-form-item>
      <el-form-item label="意向物流:" size="mini">
        {{params.intentional_logistics}}
      </el-form-item>
      <el-form-item label="详细地址:" size="mini">
        {{params.address}}
      </el-form-item>
      <el-form-item label="摘要:" size="mini">
        {{params.abstract}}
      </el-form-item>

    </el-form>
    <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData" style="width: 100%" max-height="600" :summary-method="getSummaries" show-summary @selection-change="handleSelectionChange">
      <el-table-column :show-overflow-tooltip="true" prop="business_type_name" label="业务类型" width="180">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="brand_name" label="品牌名称" width="180">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="goods_id" label="商品编号" width="180">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="goods_name" label="商品名称" min-width="500">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" property="tire_un" width="100" label="胎号">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="goods_num" label="商品数量" min-width="120" align="right">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="goods_price" label="原销售价" min-width="120" align="right">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="discount_price" label="原折后价" min-width="120" align="right">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="new_goods_price" label="最新销售价" min-width="120" align="right">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="abrasion_price" label="磨损费" min-width="120" align="right" :formatter="formatterAbrasion">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="intube_price" label="内垫费" min-width="120" align="right" :formatter="formatterIntube">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="claim_price" label="理赔单价" min-width="120" align="right" :formatter="formatterClaim">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="total_price" label="小计金额" min-width="120" align="right">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="batch" label="年周/批次" width="132">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="warehouse_name" label="仓库名称" width="132">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="allocation_name" label="货位名称" width="132">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" prop="remark" label="备注" min-width="120">
      </el-table-column>
    </el-table>

    <div class="refundauditFoot">
      <span>本单使用返利：{{params.rebate_price }}元</span>
      <span>本单使用授信：{{params.credit_price}}元</span>
      <span>本单使用预收：{{params.deposits_price }}元</span>
      <span>本单结算金额：{{params.settlement_price }}元</span>
    </div>

    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <!-- 已审核 -->
      <!-- 未审核 -->
      <template v-if="params.auditor_status == 1">
        <el-button type="primary" @click="audit('auidtorPass_Order')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('auditorInvalid_Order')" v-if="authority.b_invalid==1">作废</el-button>
      </template>

      <!-- 作废 -->
      <template v-if="params.auditor_status == 3   || (authority.b_invalid != 1 && authority.b_pass != 1 && authority.b_nopass !=1 )">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>返回</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "aftersaleThreeExamine",
  data() {
    return {
      authority: {},
      params: {},
      TableData: []
    };
  },
  computed: {
    ...mapGetters(["examine_Get", "threeorderIndex_Get"])
  },
  methods: {
    ...mapActions(["threeorderIndexACT"]),
    goBack() {
      // wz annotation cache
      this.threeorderIndexACT({
        // moduleName: "Index",
        // params: this.examine_Get.params,
        params: this.threeorderIndex_Get ? this.threeorderIndex_Get.params : {},
        goPage: this.examine_Get.goPage,
        pageSize: this.examine_Get.pageSize
      });
      this.$router.push({
        path: "/aftersale/threeorder"
      });
    },
    audit(api) {
      this.$axMethod[api]({
        order_id: this.examine_Get.order_id
      }).then(response => {
        if (response.data.code == 200) {
          this.$message({
            message: response.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    handleSelectionChange() {},
    // 合并计算
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        if (column.property === "total_price") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index]).toFixed(2);
        }
        if (column.property === "goods_num") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
        }
        // 磨损费合计
        if (column.property === "abrasion_price") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index]).toFixed(2);
        }
        //内垫费合计
        if (column.property === "intube_price") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index]).toFixed(2);
        }
        // 理赔单价合计
        if (column.property === "claim_price") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index]).toFixed(2);
        }
      });
      return sums;
    },
    formatterAbrasion(row, column, cellValue, index) {
      if (row.business_type != 1) {
        this.$set(this.TableData[index], "abrasion_price", "");
      }
      return row.abrasion_price;
    },
    formatterIntube(row, column, cellValue, index) {
      if (row.business_type != 1) {
        this.$set(this.TableData[index], "intube_price", "");
      }
      return row.intube_price;
    },
    formatterClaim(row, column, cellValue, index) {
      if (row.business_type != 1) {
        this.$set(this.TableData[index], "claim_price", "");
      }
      return row.claim_price;
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    let order_id = {
      order_id: this.examine_Get.order_id
    };
    this.$axMethod.getOrderInfo(order_id).then(({ data }) => {
      this.params = data.data.order_info;
      this.TableData = data.data.goods_list;
    });
  }
};
</script>

<style scoped>
.el-form-item--mini.el-form-item,
.el-form-item--small.el-form-item {
  margin-bottom: 0px !important;
}

.table_sum {
  width: 100%;
  overflow: hidden;
  background: #f5f7fa;
}

.table_sum li {
  float: left;
  border: 1px solid #edf0f9;
  border-top: none;
  font-size: 14px;
  color: #1f2d3d;
  line-height: 33px;
  text-align: center;
  padding: 0px 10px;
}
</style>
